import React from 'react';
import {connect} from 'react-redux'
import { withRouter } from "react-router-dom"
import { dispatchItem, userVerification } from '../../actions/actions'

class AccountActivated extends React.Component {
    constructor(props) {
      super(props);
  
      this.state = {
        status: 0
      }

      this.changeActivePopup = this.changeActivePopup.bind(this);
    }

    componentDidMount() {
      this.props.dispatch(userVerification({ }, (data) => {
        this.setState({
          status: 1
        });
      }, () => {
        this.setState({
          status: -1
        });
      }, this.props.match.params.id));
    }

    changeActivePopup(activePopup) {
      this.props.dispatch(dispatchItem('POPUP', activePopup));
    }

    render() {
      return (
        <section className="panel small">
          <div className="container">
            {this.state.status == 1 &&
              <div className="activation">
                <div className="title-default">
                  <span>Hesabn aktifletirildi!</span>
                </div>
                <div className="text">
                  Giri yapman iin ifreni telefonuna gnderdik.
                </div>
                <a className="btn primary" href="javascript:void(0)" title="Giri Yap" onClick={() => this.changeActivePopup('login')}>
                  <span>Giri Yap</span>
                </a>
              </div>
            }
            {this.state.status == -1 &&
              <div className="activation">
                <div className="title-default">
                  <span>Bir hata olutu</span>
                </div>
                <div className="text">
                  Ltfen daha sonra tekrar deneyiniz.
                </div>
              </div>
            }
          </div>
        </section>
      );
    }
  }

  function mapStateToProps(state) {
    return {
      app: state.app 
    }
  }

  export default withRouter(connect(mapStateToProps)(AccountActivated))